import React from "react";
import qs from "qs";
import { path, isEmpty, isNil } from "ramda";

import {
  lifecycle,
  compose,
  withStateHandlers,
  branch,
  renderComponent,
} from "recompose";

import BlogPost from "components/blog/BlogPost";

import { getPreviewClient } from "../utils";

import { MessageWrapper } from "./elements";

const ContentfulPreview = ({ pageData, location }) => (
  <BlogPost
    indexPage={false}
    pageContext={{ contentfulData: pageData.fields }}
    location={location}
  />
);

const _lifecycle = {
  async componentDidMount() {
    const blogPageSpaceClient = getPreviewClient(
      process.env.CONTENTFUL_BLOG_ACCESS_TOKEN_PREVIEW,
      process.env.CONTENTFUL_BLOG_SPACE_ID,
    );

    try {
      const urlSearch = qs.parse(window.location.search, {
        ignoreQueryPrefix: true,
      });

      const category = path(["category"], urlSearch);
      const url = path(["url"], urlSearch);

      if (isNil(url)) {
        throw Error(
          "Please specify a URL to test via a URL parameter (i.e. www-stg.brex.com/contentful-preview/blog-post/?category=generic&url=how-to-make-a-balance-sheet)",
        );
      }

      const entries = await blogPageSpaceClient.getEntries({
        content_type: category === "video" ? "videoPost" : "genericPost",
        "fields.urlPath": url,
        include: 4,
      });

      if (isEmpty(entries.items)) {
        throw Error(
          "The URL path that was provide was not matched to an blog post. Please try again with a different path.",
        );
      }
      // this library json-stringify-safe takes all data returned from Contentful, stringifies it and removes all circular references, which you then have to parse as an object. https://github.com/contentful/contentful.js/issues/249#issuecomment-403237882
      this.props.setPageData(entries.items[0]);
    } catch (error) {
      this.props.setError(error.message);
    }
  },
};

export default compose(
  withStateHandlers(
    {
      pageData: null,
      error: null,
    },
    {
      setPageData: () => (data) => ({ pageData: data }),
      setError: () => (errorMessage) => ({ error: errorMessage }),
    },
  ),
  lifecycle(_lifecycle),
  branch(
    ({ error }) => !!error,
    renderComponent(({ error }) => <MessageWrapper>{error}</MessageWrapper>),
  ),
  branch(
    ({ pageData }) => !pageData,
    renderComponent(() => (
      <MessageWrapper>Fetching contentful data...</MessageWrapper>
    )),
  ),
)(ContentfulPreview);
